/*----- Imports React and others -----*/
import React from 'react';
import Divider from '@material-ui/core/Divider';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Rating from '@material-ui/lab/Rating';
import { makeStyles } from '@material-ui/core/styles';

/*----- Import css -----*/
import './HardSkills.scss';

/*----- Begin class and functions -----*/
const useStyles = makeStyles((theme) => ({
    listItem: {
        paddingBottom: 0,
        paddingTop: 0
    },
}));

function HardSkills()
{
    const classes = useStyles();

    return(
        <div className="container__HardSkills">
            <h3>Hard Skills</h3>
            <Divider />
            <div className="description">
                <List>
                    <h4>Primitive Language</h4>
                    <ListItem className={classes.listItem}>
                        <ListItemText primary="C/C++ | Java | SQL" secondary={
                            <Rating name="read-only" value={4} readOnly />
                        } />
                    </ListItem>
                    <ListItem className={classes.listItem}>
                        <ListItemText primary="Go" secondary={
                            <Rating name="read-only" value={3} readOnly/>
                        } />
                    </ListItem>
                    <h4>Web Developer</h4>
                    <ListItem className={classes.listItem}>
                        <ListItemText primary="HMTL | CSS" secondary={
                            <Rating name="read-only" value={5} readOnly />
                        } />
                    </ListItem>
                    <ListItem className={classes.listItem}>
                        <ListItemText primary="JavaScript | TypeScript | Node.js | React | Libraries & Frameworks" secondary={
                            <Rating name="read-only" value={5} readOnly />
                        } />
                    </ListItem>
                    <h4>Android Developer</h4>
                    <ListItem className={classes.listItem}>
                        <ListItemText primary="Kotlin | Dart | Flutter" secondary={
                            <Rating name="read-only" value={2} readOnly />
                        } />
                    </ListItem>
                    <h4>Engenharia de Software</h4>
                    <ListItem className={classes.listItem}>
                        <ListItemText primary="Git | DevOps" secondary={
                            <Rating name="read-only" value={5} readOnly />
                        } />
                    </ListItem>
                    <ListItem className={classes.listItem}>
                        <ListItemText primary="Azure (Microsoft)" secondary={
                            <Rating name="read-only" value={3} readOnly />
                        } />
                    </ListItem>
                    <ListItem className={classes.listItem}>
                        <ListItemText primary="GCP (Google Cloud) | AWS (Amazon)" secondary={
                            <Rating name="read-only" value={1} readOnly />
                        } />
                    </ListItem>
                    <h4>IA | IoT | Big Data | Redes Móveis</h4>
                    <ListItem className={classes.listItem}>
                        <ListItemText primary="Python" secondary={
                            <Rating name="read-only" value={4} readOnly />
                        } />
                    </ListItem>
                    <ListItem className={classes.listItem}>
                        <ListItemText primary="Machine Learning | Data Science" secondary={
                            <Rating name="read-only" value={3} readOnly />
                        } />
                    </ListItem>
                </List>
            </div>
        </div>
    );
}//end HardSkills()

export default HardSkills;