/*----- Imports React and others -----*/
import React from 'react';
import Divider from '@material-ui/core/Divider';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import StarsIcon from '@material-ui/icons/Stars';
import WorkOutlineIcon from '@material-ui/icons/WorkOutline';

/*----- Import css -----*/
import './ProfessionalHistoric.scss';

/*----- Classes and Functions -----*/
function ProfessionalHistoric()
{
    return(
        <div className="container__ProfessionalHistoric">
            <h2>Historico Profissional</h2>
            <Divider />
            <div className="description">
                <List>
                    <ListItem>
                        <ListItemIcon>
                            <StarsIcon />
                        </ListItemIcon>
                        <ListItemText primary="Estágio Full-stack developer | Instituto de Gestão e Tecnologia da Informação(IGTI)" secondary="Iniciado em Janeiro de 2021 - Presente" />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon>
                            <StarsIcon />
                        </ListItemIcon>
                        <ListItemText primary="Startup Hired, Corp. | Co-Founder & CTO | Em Hiato" secondary="Fundada em Maio de 2019 - Presente" />
                    </ListItem>
                    <ListItem>
                        <ListItemIcon>
                            <WorkOutlineIcon />
                        </ListItemIcon>
                        <ListItemText primary="Monitor do curso de Ciência da Computação | Pontifícia Universidade Católica de Minas Gerais" secondary="Agosto de 2018 - Dezembro de 2018" />
                    </ListItem>
                </List>
            </div>
        </div>
    );
}//end ProfessionalHistoric()

export default ProfessionalHistoric;